<style scoped>
.titleSecond{
  background-color: aquamarine;
}
</style>
<template>
  <div id="all">
    <n-collapse id="nav" default-expanded-names="1" accordion>
      <n-collapse-item title="HTML部分" name="1">
        <n-collapse default-expanded-names="1" accordion>
          <div class="navList" @click="push(ht)" v-for="ht in Data.text.htmlP" :key=ht.id>{{parseInt(ht.id)-100}}.{{ht.title}}</div>
        </n-collapse>
      </n-collapse-item>
      <n-collapse-item title="CSS部分" name="2">
        <n-collapse default-expanded-names="1" accordion>
          <div class="navList" @click="push(cs)" v-for="cs in Data.text.cssP" :key=cs.id>{{parseInt(cs.id)-200}}.{{cs.title}}</div>
        </n-collapse>
      </n-collapse-item>
      <n-collapse-item title="JavaScript部分" name="3">
        <n-collapse default-expanded-names="1" accordion>
          <div class="navList" @click="push(js)" v-for="js in Data.text.jsP" :key=js.id>{{parseInt(js.id)-300}}.{{js.title}}</div>
        </n-collapse>
      </n-collapse-item>
      <n-collapse-item title="Vue部分" name="4">
        <n-collapse default-expanded-names="1" accordion>
          <div class="navList" @click="push(vue)" v-for="vue in Data.text.vueP" :key=vue.id>{{parseInt(vue.id)-400}}.{{vue.title}}</div>
        </n-collapse>
      </n-collapse-item>
      <n-collapse-item title="其他部分" name="5">
        <n-collapse default-expanded-names="1" accordion>
          <div class="navList" @click="push(other)" v-for="other in Data.otherP" :key=other.id>{{parseInt(other.id)-500}}.{{other.title}}</div>
        </n-collapse>
      </n-collapse-item>
    </n-collapse>
    <div id="text">
      <router-view></router-view>
    </div>
  </div>
</template>
<script setup>
import {NCollapse,NCollapseItem} from 'naive-ui'
import {reactive} from "vue"

import router from '../router'
import text from '../assets/text.js'

let Data=reactive({
  text:text
})

let push=function(obj){
  router.push({
    name:`ArticleText`,
    params:obj
  })
}
</script>
<style scoped>
#all{
  min-height: 50rem;
  display: flex;
}
#nav,#text{
  background-color: white;
  border-radius: 1rem;
}
#nav{
  padding-top: 1rem;
  padding-left: 1rem;
  max-width: 16rem;
  border:1px solid #5eafff;
  border-radius: 1rem;
}
.navList{
  height: 3rem;
  line-height: 3rem;
  text-align: left;
  margin: 0 0 0 2rem;
  cursor: pointer;
  
}
.navList:hover{
  color: rgb(255, 0, 0);
}
#text{
  width: 80rem;
  margin-left: 1rem;
  padding: 1rem;
  border:1px solid #5eafff;
  border-radius: 1rem;
}

</style>