import { createRouter, createWebHashHistory } from 'vue-router'
import Home from '../views/Home.vue'
import Article from '../views/Article.vue'
import ArticleText from "../views/ArticleText.vue";
const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/article',
    redirect: '/article/text',
    name: 'Article',
    component: Article,
    children:[
      {path:'/article/text',name:'ArticleText',component:ArticleText},
    ]
  },
  {
    path: '/tools',
    name: 'Tools',
    component: () => import('../views/Tools.vue')
  }
]

const router = createRouter({
  history: createWebHashHistory(),
  routes
})

export default router
